<template>
  <div class="search-result">
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text=""
      @load="onLoad"
    >
      <van-cell
        v-for="(article, index) in list"
        :key="index"
        :title="article.title"
      />
    </van-list>
  </div>
</template>

<script>
import { getSearchResult } from '@/api/search'

export default {
  name: 'SearchResult',
  components: {},
  props: {
    searchText: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      page: 1, // 
      perPage: 20 // 
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    async onLoad () {
      // 1. 
      const { data } = await getSearchResult({
        page: this.page, // 
        per_page: this.perPage, // 
        q: this.searchText // 
      })

      // 2. 
      const { results } = data.data
      this.list.push(...results)

      // 3.  loading
      this.loading = false

      // 4. 
      if (results.length) {
        // 
        this.page++
      } else {
        //  finished  true
        this.finished = true
      }
    }
  }
}
</script>

<style scoped lang="less">
.search-result {
  position: fixed;
  left: 0;
  right: 0;
  top: 54px;
  bottom: 0;
  overflow-y: auto;
}
</style>
